import React from "react";
import { Container, Col, Row, Card, Button } from "react-bootstrap";
import assets from "../assets/assets.json";
export default function Certificates() {
  return (
    <Container className="p-3">
      <Row xs={12} sm={12} md={12} lg={4} className="g-4">
        {assets.certificates.map((certificate, index) => (
          <Col key={index}>
            <Card className="rounded-3 h-100">
              <Card.Body>
                <Card.Title>{certificate.title}</Card.Title>
                <Card.Text>{certificate.org}</Card.Text>
                <Card.Text>{certificate.date}</Card.Text>
                <Card.Link
                  target="_blank"
                  className="btn btn-outline-primary "
                  href={
                    process.env.PUBLIC_URL +
                    "/assets/certificates/" +
                    certificate.path
                  }
                >
                  View
                </Card.Link>
                {certificate.url && (
                  <Card.Link
                    target="_blank"
                    className="btn btn-outline-primary "
                    href={certificate.url}
                  >
                    Verify
                  </Card.Link>
                )}
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
}
